<script>
import { spotifyAction } from "@/assets/action";
// import { hueUpdate } from "@/assets/update";

export default {
    name: "SpotifyWidget",
    data() {
        return {
            id: null,
            name: null,
            spotInfo: {},
            oAuth: null,
            deviceId: null,

            isPlay: false,
        };
    },
    components: {
    },
    props: {
        widgetProps: {}
    },
    beforeMount() {
        this.oAuth = this.widgetProps.parameter.oAuth;
        this.deviceId = this.widgetProps.parameter.deviceId;

    },
    mounted() {

    },
    beforeDestroy() {
    },
    methods: {
        btnPlay() {
            if (this.isPlay) {
                spotifyAction("pause", this.oAuth, this.deviceId);
                this.isPlay = false;
            } else {
                spotifyAction("play", this.oAuth, this.deviceId);
                this.isPlay = true;
            }
        },
        btnNext() {
            spotifyAction("next", this.oAuth, this.deviceId);
        },
        btnBack() {
            spotifyAction("back", this.oAuth, this.deviceId);
        }
    },
};
</script>

