import { createRouter, createWebHashHistory, createWebHistory, RouteRecordRaw } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Dashboard from '../views/Dashboard/TestDashboard.vue'

const router = createRouter({
  history: createWebHashHistory(),
  routes: [
    {
      path: '/',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
    },
    {
      path: '/Connexion',
      name: 'connexion',
      component: () => import(/* webpackChunkName: "about" */ '../views/Connexion.vue')
    },
    {
      path: '/ConnexionCheck',
      name: 'ConnexionCheck',
      component: () => import(/* webpackChunkName: "about" */ '../views/CheckScreen/ConnexionCheck.vue')
    },
    {
      path: '/ProfileEdit',
      name: 'ProfileEdit',
      component: () => import(/* webpackChunkName: "about" */ '../views/Dashboard/ProfileEdit.vue')
    },
    {
      path: '/EditDashboard',
      name: 'EditDashboard',
      component: () => import(/* webpackChunkName: "about" */ '../views/Dashboard/EditDashboard.vue')
    },
    {
      path: '/Home',
      name: 'Homescreen',
      component: () => import(/* webpackChunkName: "about" */ '../views/Dashboard/HomeScreen.vue')
    },
    {
      path: '/addWidget',
      name: 'addWidget',
      component: () => import(/* webpackChunkName: "about" */ '../views/Dashboard/addWidget.vue')
    },
    {
      path: '/WeatherConfig',
      name: 'WeatherConfig',
      component: () => import(/* webpackChunkName: "about" */ '../views/Dashboard/configWidgets/WeatherConfig.vue')
    },
    {
      path: '/HueConfig',
      name: 'HueConfig',
      component: () => import(/* webpackChunkName: "about" */ '../views/Dashboard/configWidgets/HueConfig.vue')
    },
    {
      path: '/SpotifyConfig',
      name: 'SpotifyConfig',
      component: () => import(/* webpackChunkName: "about" */ '../views/Dashboard/configWidgets/SpotifyConfig.vue')
    },
    {
      path: '/CalendarGridConfig',
      name: 'CalendarGridConfig',
      component: () => import(/* webpackChunkName: "about" */ '../views/Dashboard/configWidgets/CalendarGridConfig.vue')
    },
    {
      path: '/EventConfig',
      name: 'EventConfig',
      component: () => import(/* webpackChunkName: "about" */ '../views/Dashboard/configWidgets/EventConfig.vue')
    },
  ]
});

// const router = createRouter({
//     history: createWebHistory(process.env.BASE_URL),
//     routes
//   })

export default router;
