<template>
    <div class="top-space ">
        <div class="widget d-flex flex-column posSet">
            <strong class="title">{{ this.name }}</strong>
            <div>
                <WeatherWidget v-if="isWeatherWidget" :widgetProps="widgetInfo" />
                <HueWidget v-if="isHueWidget" />
                <SpotifyWidget v-if="isSpotifyWidget"  :widgetProps="widgetInfo" />
                <CalendarWidget v-if="isCalendarWidget"  />
                <EventWidget v-if="isEventWidget"  />
            </div>
        </div>
    </div>
</template>

<script>
import WeatherWidget from "@/components/dashboard/widgets/specWidget/Weather.vue"
import HueWidget from "@/components/dashboard/widgets/specWidget/Hue.vue"
import SpotifyWidget from "@/components/dashboard/widgets/specWidget/Spotify.vue"
import CalendarWidget from "@/components/dashboard/widgets/specWidget/CalendarGrid.vue"
import EventWidget from "@/components/dashboard/widgets/specWidget/Event.vue"

export default {
    name: "genericWidget",
    data() {
        return {
            id: null,
            name: null,
            bgColor: "#145374",

            isWeatherWidget: false,
            isHueWidget: false,
            isSpotifyWidget: false,
            isCalendarWidget: false,
            isEventWidget: false,
        };
    },
    components: {
        WeatherWidget,
        HueWidget,
        SpotifyWidget,
        CalendarWidget,
        EventWidget
    },
    props: {
        widgetInfo: {}
    },
    beforeMount() {
    },
    mounted() {
        this.name = this.widgetInfo.name;
        this.id = this.widgetInfo.widgetId;
        this.bgColor = this.widgetInfo.bgcolor;
        console.log("Widget : ", this.widgetInfo);

        switch (this.id) {
            case "629a5ed82f6d9bf0c71333f9": //weather
                this.isWeatherWidget = true;
                break;
            case "629a65232f6d9bf0c71333fa": //hue
                this.isHueWidget = true;
                break;
            case "62a38edb9bcab46134b480d6": //Spotify
                this.isSpotifyWidget = true;
                break;
            case "62a3b1409bcab46134b480d7": //Calendar
                this.isCalendarWidget = true;
                break;
            case "62a4451946d679d1f921c81d": //Event
                this.isEventWidget = true;
                break;

            default:
                break;
        }
    },
    methods: {
    },
};
</script>

<style scoped>
.widget {
    border-radius: 12px;
    height: 200px;
    width: 200px;
    background-color: v-bind(bgColor);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #ffffff;
}

.title {
    position: absolute;
    top: 20px;
    left: 20px;

}

.posSet {
    position: relative;
}

.top-space {
    padding-top: 30px;
}
</style>