import axios from "axios"

let reponseToReturn = null;
export const hueAction = async (lampNumber, state) => {
    const token = "SFgSPQaD7hLUCHYk6KE-kNYqKZeG3qef9rXpZqMa";
    const urlTouse = `https://192.168.1.240/api/${token}/lights/${lampNumber}/state`
    await axios({
        method: "put",
        url: urlTouse,
        data: { "on": state },
    })
        .then((response) => {
            console.log("res 200 from js file : ", response.data);
            reponseToReturn = response.data;
        })
        .catch((response) => {
            console.log("res error from js file: ", response.response.data.message);
        });
    return reponseToReturn;
}

export const spotifyAction = async (action, oAuthKey, deviceId) => {
    const playSpot = async () => {
        const authorizationKey = `Bearer ${oAuthKey}`
        const urlTouse = `https://api.spotify.com/v1/me/player/play`
        await axios({
            method: "put",
            url: urlTouse,
            headers: {
                "Accept": "application/json",
                "Content-Type": "application/json",
                "Authorization": authorizationKey,
            },
            data: { "device_id": deviceId },
        })
            .then((response) => {
                console.log("res 200 from js file : ", response.data);
                reponseToReturn = response.data;
            })
            .catch((response) => {
                console.log("res error from js file: ", response.response.data.message);
            });
    }
    const pauseSpot = async () => {
        const authorizationKey = `Bearer ${oAuthKey}`
        const urlTouse = `https://api.spotify.com/v1/me/player/pause`
        await axios({
            method: "put",
            url: urlTouse,
            headers: {
                "Accept": "application/json",
                "Content-Type": "application/json",
                "Authorization": authorizationKey,
            },
            data: { "device_id": deviceId },
        })
            .then((response) => {
                console.log("res 200 from js file : ", response.data);
                reponseToReturn = response.data;
            })
            .catch((response) => {
                console.log("res error from js file: ", response.response.data.message);
            });
    }
    const nextSpot = async () => {
        const authorizationKey = `Bearer ${oAuthKey}`
        const urlTouse = `https://api.spotify.com/v1/me/player/next`
        await axios({
            method: "post",
            url: urlTouse,
            headers: {
                "Accept": "application/json",
                "Content-Type": "application/json",
                "Authorization": authorizationKey,
            },
            data: { "device_id": deviceId },
        })
            .then((response) => {
                console.log("res 200 from js file : ", response.data);
                reponseToReturn = response.data;
            })
            .catch((response) => {
                console.log("res error from js file: ", response.response.data.message);
            });
    }
    const previousSpot = async () => {
        const authorizationKey = `Bearer ${oAuthKey}`
        const urlTouse = `https://api.spotify.com/v1/me/player/previous`
        await axios({
            method: "post",
            url: urlTouse,
            headers: {
                "Accept": "application/json",
                "Content-Type": "application/json",
                "Authorization": authorizationKey,
            },
            data: { "device_id": deviceId },
        })
            .then((response) => {
                console.log("res 200 from js file : ", response.data);
                reponseToReturn = response.data;
            })
            .catch((response) => {
                console.log("res error from js file: ", response.response.data.message);
            });
    }

    switch (action) {
        case "play":
            playSpot();
            break;
        case "pause":
            pauseSpot();
            break;
        case "next":
            nextSpot();
            break;
        case "back":
            previousSpot();
            break;

        default:
            break;
    }
    return reponseToReturn;
}